/**
 * @file KLSearch 
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLSearch
 * @extend Component
 * @param {object}          [options.data]                       = 
 * @param {string}          [options.data.class]                 => class
 * @param {boolean}         [options.data.isShowFooter]          => Footer
 * @param {boolean}         [options.data.isShowMore]            => 
 * @param {boolean}         [options.data.isShowToggle]          => toggle
 * @param {string}          [options.data.searchText]            => 
 * @param {string}          [options.data.resetText]             => 
 * @param {string}          [options.data.unfoldText]            => 
 * @param {string}          [options.data.foldText]              => 
 */
const KLSearch = Component.extend({
  name: 'kl-search',
  template,
  $more: null,

  config() {
    _.extend(this.data, {
      isShowMore: false,
      isShowFooter: true,
      isShowToggle: true,
      searchText: this.$trans('SEARCH'),
      resetText: this.$trans('RESET'),
      unfoldText: this.$trans('UNFOLD'),
      foldText: this.$trans('FOLD'),
      toggleText: this.$trans('UNFOLD'),
    });
    this.data.toggleText = this.data.isShowMore ? this.data.foldText : this.data.unfoldText;
    this.supr();
  },
  toggle() {
    const data = this.data;
    data.isShowMore = !data.isShowMore;
    data.toggleText = data.toggleText === data.unfoldText ? data.foldText : data.unfoldText;
  },
  /**
  * @event KLSearch#search 
  */
  search() {
    this.$emit('search');
  },
  /**
  * @event KLSearch#reset 
  */
  reset() {
    this.$emit('reset');
  },
});

module.exports = KLSearch;



