
const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLMessage
 * @extend Component
 * @param {object}      [options.data]                      = 
 * @param {string}      [options.data.type]                 => `success``warning``notice``error`
 * @param {string}      [options.data.class]                => `class`
 * @param {string}      [options.data.title]                => `title`
 * @param {string}      [options.data.addClose]                => `addClose`  , message
 */

const KLMessage = Component.extend({
  name: 'kl-message',
  template,
  config() {
    _.extend(this.data, {
      type: '',
      addClose: false,
      title: '',
      visible: true,
    });
    this.supr();
  },
  init() {
    this.supr();
  },
  close(result) {
    this.data.visible = Boolean(result);
    this.$emit('close');
    this.$update();
  },
});
module.exports = KLMessage;



