/**
 * @file KLCheck 
 * @author sensen <rainforest92@126.com>
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLCheck
 * @extend Component
 * @param {object}      options.data                    = 
 * @param {string}      [options.data.name]             => 
 * @param {boolean}     [options.data.checked=false]    <=> `false``true``null`
 * @param {boolean}     [options.data.block=false]      => block
 * @param {boolean}     [options.data.readonly=false]   => 
 * @param {boolean}     [options.data.disabled=false]   => 
 * @param {boolean}     [options.data.visible=true]     => 
 * @param {string}      [options.data.class]            => class
 * @param {string}      [options.data.contentTemplate]  => 
 */
const KLCheck = Component.extend({
  name: 'kl-check',
  template,
  config() {
    _.extend(this.data, {
      name: '',
      checked: false,
      block: false,
    });
    this.supr();

    this.$watch('checked', function (newValue, oldValue) {
      if (oldValue === undefined) return;

      /**
         * @event KLCheck#change 
         * @property {object} sender 
         * @property {object} date 
         */
      this.$emit('change', {
        sender: this,
        checked: newValue,
      });
    });
  },

  check(_checked, e) {
    if (this.data.readonly || this.data.disabled) return;

    let checked = _checked;

    if (checked === undefined) checked = !this.data.checked;
    this.data.checked = checked;

    /**
       * @event KLCheck#check 
       * @property {object} sender 
       * @property {boolean} checked 
       */
    this.$emit('check', {
      sender: this,
      checked,
      e,
    });
    return false;
  },
});

module.exports = KLCheck;



