/**
 * @file KLRow -
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');

/**
 * @class KLRow
 * @extend Component
 * @param {object}          [options.data]                        => 
 * @param {string}          [options.data.class]                  => class
 * @param {string}          [options.data.type]              =>  flex
 * @param {string}          [options.data.justify=start]      => flex 
 * @param {string}          [options.data.align=top]          => flex 
 * @param {string}          [options.data.wrap=wrap]          => flex,wrap/nowrap/wrap-reverse
 * @param {number}          [options.data.gutter=40]           => , px
 */

/**
 * @class KLCol
 * @extend Component
 * @param {object}          [options.data]                        => 
 * @param {string}          [options.data.class]                  => class
 * @param {number}          [options.data.span]              => 
 * @param {number}          [options.data.offset]            => 
 * @param {number/object}   [options.data.xs]                => <768px , number/object ( {span: 4, offset: 4})
 * @param {number/object}   [options.data.sm]                => 768px , number/object ( {span: 4, offset: 4})
 * @param {number/object}   [options.data.md]                => 992 , number/object ( {span: 4, offset: 4})
 * @param {number/object}   [options.data.lg]                => 1200 , number/object ( {span: 4, offset: 4})
 */

const KLRow = Component.extend({
  name: 'kl-row',
  template,
  config(data) {
    this.defaults({
      type: '',
      justify: 'start',
      align: 'top',
      gutter: 24,
      wrap: 'wrap',
    });

    this.supr(data);
  },
});

KLRow.directive('gutter', function (ele, value) {
  this.$watch(value, (gutter) => {
    if (gutter || gutter === 0) {
      const margin = `-${gutter / 2}px`;
      ele.style.marginLeft = margin;
      ele.style.marginRight = margin;
    }
  });
});

module.exports = KLRow;



