/**
 * @file TreeView  
 * @author   sensen(rainforest92@126.com)
 */

const SourceComponent = require('../../../ui-base/sourceComponent');
const KLTree = require('./KLTree');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLTreeView
 * @param {object}    [options.data]                          = 
 * @param {object[]}  [options.data.source=[]]                <=> 
 * @param {string}    [options.data.source[].name]            => 
 * @param {string}    [options.data.key=id]                   => 
 * @param {string}    [options.data.nameKey=name]             => 
 * @param {string}    [options.data.childKey=children]        => 
 * @param {boolean}   [options.data.source[].open=false]      => /
 * @param {boolean}   [options.data.source[].checked=false]   => 
 * @param {boolean}   [options.data.source[].disabled=false]  => 
 * @param {string}    [options.data.value=null]               <=> 
 * @param {string}    [options.data.separator=,]              => value
 * @param {boolean}   [options.data.multiple=false]           => 
 * @param {boolean}   [options.data.readonly=false]           => 
 * @param {boolean}   [options.data.disabled=false]           => 
 * @param {boolean}   [options.data.visible=true]             => 
 * @param {string}    [options.data.class]                    => class
 */
const KLTreeView = SourceComponent.extend({
  name: 'kl-tree-view',
  template,
  config() {
    _.extend(this.data, {
      // @inherited source: [],
      key: 'id',
      nameKey: 'name',
      childKey: 'children',
      separator: ',',
      value: null,
      selected: null,
      multiple: false,
      hierarchical: false,
    });
    this.supr();
    this.$ancestor = this;
    this.$watch('selected', function (newVal) {
      const { key, nameKey, separator } = this.data;
      if (!newVal) return;
      if (Array.isArray(newVal)) {
        return (this.data.value = newVal
          .map(d => d[key] || d[nameKey])
          .join(separator));
      }
      this.data.value = newVal[key] || newVal[nameKey];
    });
  },
  select(item) {
    if (!this.data.multiple && (
      this.data.readonly ||
      this.data.disabled ||
      item.disabled
    )) {
      return;
    }

    this.toggle(item);
    if (this.data.multiple) return (item.selected = !item.selected);

    this.data.selected = item;
    /**
       * @event KLTreeView#select 
       * @property {object} sender 
       * @property {object} selected 
       */
    this.$emit('select', {
      sender: this,
      selected: item,
    });
  },
  toggle(item, _open) {
    if (!this.data.multiple && (
      this.data.readonly ||
      this.data.disabled ||
      item.disabled
    )) {
      return;
    }

    let open = _open;
    if (open === undefined) open = !item.open;
    item.open = open;

    /**
       * @event KLTreeView#toggle 
       * @property {object} sender 
       * @property {object} item 
       * @property {boolean} open /
       */
    this.$emit('toggle', {
      sender: this,
      item,
      open,
    });
  },
  _getSelected(source) {
    const self = this;
    if (!source) return [];
    let arr = [];
    source.forEach((d) => {
      const child = d[self.data.childKey];
      if (child && child.length) {
        arr = arr.concat(self._getSelected(d[self.data.childKey]));
      } else if (d.checked) {
        arr = arr.concat(d);
      }
    });
    return arr;
  },
  setSelected() {
    this.data.selected = this._getSelected(this.data.source);
  },
});

KLTreeView.component('kl-tree', KLTree);
module.exports = KLTreeView;



