/**
 * @file KLText  
 * @author   Cody Chan<int64ago@gmail.com>
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLText
 * @extend Component
 * @param {object}      [options.data]                = 
 * @param {string}      [options.data.text=]      <=> 
 * @param {string}      [options.data.size]           => text xssmlgxl
 * @param {boolean}     [options.data.isBold=false]   =>  true- false-
 * @param {string}      [options.data.align]          =>  leftcenterright
 * @param {string}      [options.data.vertical]       =>  topmiddlebottom
 * @param {string}      [options.data.type=default]   =>  defaultprimarysuccesswarningerrorinversemuted
 * @param {string}      [options.dara.class]          => class
 * @param {string}      [options.data.url=javascript:;] =>url javascript:
 * @param {string}      [options.data.target]        => atarget _blank_self_parent_top
 */
const KLText = Component.extend({
  name: 'kl-text',
  template,
  config() {
    _.extend(this.data, {
      text: '-',
      // default/primary/info/success/warning/error
      type: 'default',
      // xs/sm/lg/xl
      size: '',
      isBold: false,
      // tal/tac/tar/taj
      align: '',
      // vat/vam/vab
      vertical: '',
      /* eslint no-script-url: 0 */
      url: 'javascript:;',
      target: '',
    });
    this.supr();
  },
  onClick(e) {
    this.$emit('click', e);
  },
});

module.exports = KLText;



