/**
 * ------------------------------------------------------------
 * Suggest   
 * @author   sensen(rainforest92@126.com)
 * ------------------------------------------------------------
 */

const Dropdown = require('../../form/common/Dropdown');
const template = require('./index.html');
const _ = require('../../../ui-base/_');
const Validation = require('../../../util/validation');
const validationMixin = require('../../../util/validationMixin');

/**
 * @class Suggest
 * @extend Dropdown
 * @param {object}        [options.data]                          = 
 * @param {object[]}      [options.data.source=[]]                <=> 
 * @param {string}        [options.data.source[].name]            => 
 * @param {string}        [options.data.key=id]                   => 
 * @param {string}        [options.data.nameKey=name]             => name
 * @param {boolean}       [options.data.source[].disabled=false]  => 
 * @param {object}        [options.data.selected=null]            <=> 
 * @param {string}        [options.data.value]                    <=> 
 * @param {string}        [options.data.id]                       <=> id;
 * @param {string}        [options.data.placeholder=]      => 
 * @param {number}        [options.data.maxlength]                => 
 * @param {number}        [options.data.startLength=0]            => >=
 * @param {string}        [options.data.matchType=all]            => `all``start``end`
 * @param {boolean}       [options.data.strict]                   => `value`source
 * @param {boolean}       [options.data.autofocus]                => 
 * @param {string}        [options.data.itemTemplate=null]        @=> 
 * @param {boolean}       [options.data.hideTip=false]            => 
 * @param {boolean}       [options.data.open]                     <=> /
 * @param {boolean}       [options.data.readonly]                 => 
 * @param {boolean}       [options.data.disabled]                 => 
 * @param {boolean}       [options.data.visible=true]             => 
 * @param {string}        [options.data.class]                 => class
 * @param {object}        [options.service]                       @=> 
 */
const Suggest = Dropdown.extend({
  name: 'suggest',
  template,
  /**
     * @protected
     */
  config() {
    _.extend(this.data, {
      // @inherited source: [],
      // @inherited open: false,
      hideTip: false,
      selected: null,
      value: '',
      id: '',
      key: 'id',
      nameKey: 'name',
      placeholder: this.$trans('PLEASE_SELECT'),
      maxlength: undefined,
      startLength: 0,
      delay: 300,
      matchType: 'all',
      strict: false,
      autofocus: false,
      required: false,
    });
    this.supr();

    this.initValidation();

    this.$watch('id', function (id) {
      let source = this.data.source || [],
        key = this.data.key,
        nameKey = this.data.nameKey;

      const crt = source.find(item => item[key] == id);
      this.$update('value', crt ? crt[nameKey] : '');
    });
  },
  /**
     * @protected
     */
  init() {
    let id = this.data.id,
      source = this.data.source;

    if (id && source) {
      const selected = source.filter(function (item) {
        return item[this.data.key] == id;
      })[0];
      if (selected) {
        this.data.selected = selected;
        this.data.value = selected[this.data.nameKey];
      }
    }
  },
  /**
     * @method select(item) 
     * @public
     * @param  {object} item 
     * @return {void}
     */
  select(item) {
    if (
      this.data.readonly ||
      this.data.disabled ||
      item.disabled ||
      item.divider
    ) {
      this.$emit('select', {
        sender: this,
        selected: item,
        disabled: true,
      });
      return;
    }

    this.data.selected = item;
    this.data.value = item[this.data.nameKey];
    this.data.id = item[this.data.key];

    /**
         * @event select 
         * @property {object} sender 
         * @property {object} selected 
         */
    this.$emit('select', {
      sender: this,
      selected: item,
    });
    this.toggle(false);
  },
  /**
     * @method toggle(open) /
     * @public
     * @param  {boolean} open /
     * @return {void}
     */
  toggle(open, _isInput) {
    if (this.data.readonly || this.data.disabled) return;

    if (open === undefined) open = !this.data.open;
    this.data.open = open;

    const index = Dropdown.opens.indexOf(this);
    if (open && index < 0) Dropdown.opens.push(this);
    else if (!open && index >= 0) {
      Dropdown.opens.splice(index, 1);

      if (!_isInput && this.data.strict) {
        this.data.value = this.data.selected
          ? this.data.selected[this.data.nameKey]
          : '';
      }
    }

    /**
         * @event toggle  /
         * @property {object} sender 
         * @property {object} open /
         */
    this.$emit('toggle', {
      sender: this,
      open,
    });
  },
  /**
     * @private
     */
  _onInput($event) {
    const value = this.data.value || '';

    if (value.length >= this.data.startLength) {
      this.toggle(true);
      if (this.service) this.$updateSource();
    } else this.toggle(false, true);
  },
  /**
     * @private
     */
  _onBlur($event) {},
  /**
     * @private
     */
  getParams() {
    return { value: this.data.value };
  },
  /**
     * @private
     */
  filter(item) {
    const value = this.data.value;

    if (!value && this.data.startLength) return false;

    if (this.data.matchType === 'all') {
      return item[this.data.nameKey].indexOf(value) >= 0;
    } else if (this.data.matchType === 'startLength') {
      return item[this.data.nameKey].slice(0, value.length) === value;
    } else if (this.data.matchType === 'end') {
      return item[this.data.nameKey].slice(-value.length) === value;
    }
  },
  /**
     * @method validate() 
     * @public
     * @return {object} result 
     */
  validate(on) {
    if (!this.data.required) {
      return { success: true };
    }

    var result = { success: true, message: '' },
      value = this.data.value,
      value = typeof value === 'undefined' ? '' : `${value}`;

    if (!value.length) {
      result.success = false;
      result.message = this.data.message || this.$trans('PLEASE_SELECT');
      this.data.state = 'error';
    } else {
      result.success = true;
      result.message = '';
      this.data.state = '';
    }
    this.data.tip = result.message;

    this.$emit('validate', {
      sender: this,
      on,
      result,
    });

    return result;
  },
});

Suggest.use(validationMixin);
module.exports = Suggest;



