
const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLMask KLLoading, 
 * @extend Component
 * @param {object}            [options.data]                      = 
 * @param {string}            [options.data.content]              => ,html
 * @param {boolean}           [options.data.closable=true]       => `true``false`
 * @param {string}            [options.data.class]                => `class`
 */
const KLMask = Component.extend({
  name: 'kl-mask',
  template,
  config() {
    _.extend(this.data, {
      closable: true,
    });
    this.supr();
  },

  init() {
    this.supr();

    // document.body
    if (this.$root === this) this.$inject(document.body);
  },

  _handleClick(e) {
    if (/m-mask/.test(e.target.className)) {
      if (this.data.closable) this.close();
    }
  },
  /**
     * @method close(data) 
     * @public
     * @param  {object} data 
     * @return {void}
     */
  close(data) {
    /**
         * @event close 
         * @property {object} data  {sender, data} 
         */
    this.$emit('close', {
      sender: this,
      data,
    });
    this.destroy();
  },
});

module.exports = KLMask;



