// /**
//  * @filter  KLModal     
//  * @author  sensen(rainforest92@126.com)
//  */

const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLModal
 * @extend Component
 * @param {object}            [options.data]                      = 
 * @param {string}            [options.data.modalType=]             => , `default``notice``warning``error``success`
 * @param {string}            [options.data.title=]            => 
 * @param {string}            [options.data.content]              => 
 * @param {string}            [options.data.contentTemplate]      => 
 * @param {string}            [options.data.footerTemplate]       => 
 * @param {boolean}           [options.data.okDisabled=false]     => Disable 
 * @param {boolean}           [options.data.cancelDisabled=false] => Disable 
 * @param {boolean}           [options.data.hasFooter=true]       =>  footer
 * @param {boolean}           [options.data.isCanClose=true]      => 
 * @param {string/boolean}    [options.data.okButton=true]        => `string`
 * @param {string/boolean}    [options.data.cancelButton=false]   => `string`
 * @param {string}            [options.data.class]                => class
 * @param {boolean}           [options.data.noClose]              => ok
 * @param {number}            [options.data.minHeight]            => 
 * @param {number}            [options.data.maxHeight]            => 
 * @param {string}            [options.data.el]                   => document.body
 * @param {boolean}           [options.data.draggable=false]      => 
 * @param {boolean}           [options.data.fullscreen=false]     => 
 */
const KLModal = Component.extend({
  name: 'kl-modal',
  template,
  config() {
    _.extend(this.data, {
      title: this.$trans('NOTICE'),
      content: '',
      modalType: '',
      okButton: true,
      width: 400,
      cancelButton: false,
      noClose: false,
      okDisabled: false,
      cancelDisabled: false,
      hasFooter: true,
      isCanClose: true,
      fullscreen: false,
    });
    this.supr();
  },

  init() {
    // - Modal
    document.body.style.overflow = 'hidden';
    this.supr();
    // data.eldocument.body
    if (this.$root === this) {
      this.$inject(this.data.el || document.body);
    }
    this.$on('$destroy', () => {
      document.body.style.overflow = '';
    });
  },
  /**
     * @method KLModal#close(result) 
     * @param  {boolean} result 
     * @return {void}
     */
  close(result, event) {
    /**
         * @event KLModal#close  
         * @property {boolean} result /
         */
    this.$emit('close', {
      result,
    });
    result ? this.ok(event) : this.cancel();
  },
  /**
     * @method KLModal#ok() 
     * @return {void}
     */
  ok(event) {
    /**
         * @event KLModal#ok 
         */
    this.$emit('ok', event);
    if (!this.data.noClose) {
      this.destroy();
      document.body.style.overflow = '';
    }
  },
  /**
     * @method KLModal#cancel() 
     * @return {void}
     */
  cancel() {
    /**
         * @event KLModal#cancel 
         */
    this.$emit('cancel');
    this.destroy();
    // - Modal
    document.body.style.overflow = '';
  },
  _onDragStart($event) {
    const dialog = $event.proxy;
    dialog.style.left = `${dialog.offsetLeft}px`;
    dialog.style.top = `${dialog.offsetTop}px`;
    dialog.style.zIndex = '1000';
    dialog.style.position = 'absolute';
  },
});

/**
 * @static
 * @param  {string} content 
 * @param  {string} title= 
 */
KLModal.alert = function (content, title, okButton) {
  const modal = new this({
    data: {
      content,
      title,
      okButton,
    },
  });

  return modal;
};

/**
 * @static
 * @param  {string} content 
 * @param  {string} title= 
 */
KLModal.confirm = function (content, title, okButton, cancelButton) {
  const modal = new this({
    data: {
      content,
      title,
      okButton,
      cancelButton: cancelButton || true,
    },
  });

  return modal;
};

module.exports = KLModal;



