/**
 * @file KLSidebar
 * @author   sensen(rainforest92@126.com)
 */
const scrollIntoViewFn = require('scroll-into-view');

const Component = require('../../../ui-base/component');
const searchableMixin = require('./mixins/searchable');
const template = require('./index.html');

/**
 * @class KLSidebar
 * @extend Component
 * @param {object}        [options.data]                          => 
 * @param {string}        [options.data.class]                    => class
 * @param {array}         [options.data.menus]                    => 
 * @param {string}        [options.data.top=60px]                 => style top
 * @param {boolean}       [options.data.uniqueOpened=true]        => 
 * @param {string}        [options.data.titleKey=title]           => key
 * @param {string}        [options.data.urlKey=url]               => key
 * @param {string}        [options.data.routeKey=route]           => spa,
 * @param {string}        [options.data.pageKey=title]            => key
 * @param {string}        [options.data.childrenKey=children]     => key
 * @param {string}        [options.data.todoKey=todos]            => ,key
 * @param {object}        [options.data.router]                   => , regular-statemanager
 * @param {string}        [options.data.width]                    => sidebar,181px
 * @param {string}        [options.data.theme]                    => sidebarlight/darklight
 * @param {boolean}       [options.data.scrollIntoView=false]     => scrollIntoView
 */
const KLSidebar = Component.extend({
  name: 'kl-sidebar',
  template,
  config() {
    this.defaults({
      class: '',
      uniqueOpened: true,
      menus: [],
      titleKey: 'title',
      urlKey: 'url',
      routeKey: 'route',
      pageKey: 'title',
      childrenKey: 'children',
      todoKey: 'todos',
      top: '60px',
      width: '216px',
      theme: 'light',
    });

    this.supr();
  },
  init() {
    const { scrollIntoView } = this.data;
    scrollIntoView && setTimeout(() => {
      scrollIntoViewFn(document.querySelector('.kl-sidebar .kl-sub-menu .kl-menu-item.is-active'), {
        validTarget(target, parentsScrolled) {
          return parentsScrolled < 2 && target !== window && target.matches('.kl-scroll-wrap');
        },
      });
    }, 50);
    this.supr();
  },
  /**
   * @event KLSidebar#menuitem-click 
   * @property {object} menuitem menuItem
   */
  onMenuItemClick(e) {
    this.$emit('menuitem-click', e);
  },
  getTitleTemplate(name, todos) {
    const title = this.highlight(name);
    if (todos && !isNaN(todos)) {
      const count = todos > 99 ? '99+' : todos;
      return `<span class="kl-sidebar-todowrap"><span class="kl-sidebar-todowrap__title">${title}</span><span class="kl-sidebar-todowrap__badge">${count}</span></span>`;
    }
    return title;
  },
  getScrollMaxHeight() {
    const { menuWrap, searchBox } = this.$refs;
    if (menuWrap && searchBox) {
      return menuWrap.clientHeight - searchBox.clientHeight;
    } else if (menuWrap) {
      return menuWrap.clientHeight;
    }
  },
});

KLSidebar.use(searchableMixin);

module.exports = KLSidebar;



