/**
 * @file kl-upload 
 */

const _ = require('../../../ui-base/_');
const utils = require('./utils');
const Config = require('./config');
const Component = require('../../../ui-base/component');
const UploadList = require('./components/UploadList');
const UploadCard = require('./components/UploadCard');
const validationMixin = require('../../../util/validationMixin');
const tpl = require('./index.html');

/**
 * @class KLUpload
 * @extend Component
 * @param {object}     [options.data]                      = 
 * @param {string}     [options.data.action]               => 
 * @param {array}      [options.data.file-list]            => , demo
 *                                                             :
 *                                                             name: 
 *                                                             url: 
 *                                                             flag: 0, ; 1, 2
 * @param {string}     [options.data.name=file]            => , 'file'
 * @param {object}     [options.data.headers]              => 
 * @param {object}     [options.data.with-credentials=false]    =>  cookie , false
 * @param {boolean}    [options.data.multiple=false]       => , true/falsefalse
 * @param {object}     [options.data.data]                 => 
 * @param {boolean}    [options.data.drag=false]           => true/falsefalse
 * @param {string}     [options.data.accept=*]             => , inputaccept
 * @param {string}     [options.data.list-type=list]       => , list/cardlist
 * @param {number}     [options.data.num-min=-Infinity]    => 
 * @param {number}     [options.data.num-max=Infinity]     => 
 * @param {number}     [options.data.num-perline]          => 
 *                                                             5
 * @param {string}     [options.data.max-size=1GB]         => , KB,MB,GB
 * @param {boolean}    [options.data.readonly=false]       => true/falsetrue
 *                                                             false
 * @param {boolean}    [options.data.hideTip=false]        => false
 * @param {number}     [options.data.image-width]          => , px800
 * @param {number}     [options.data.image-height]         => , px, 600
 * @param {string}     [options.data.image-scale]          => , '4:3'
 * @param {string}     [options.data.class]                => 
 * @param {function}   [options.data.onLoadInterceptor=NULL]    => Http status200-300response.codedemo
 * @param {function}   [options.data.onErrorInterceptor=NULL]   => Http status200-300http
 * @param {function}   [options.data.before-upload]        =>  Promise
 *                                                             
 *                                                             
 * @param {function}   [options.data.before-remove]        => remove
 *                                                             Promise false
 * @param {boolean}    [options.data.autoUpload=false]     => false, formData
 * @param {FormData}    [options.data.formData=FormData]    => autoUploadfalseformData
 *                                                             formData
 */

const KLUpload = Component.extend({
  name: 'kl-upload',
  template: tpl.replace(/([>}])\s*([<{])/g, '$1$2'),
  config(data) {
    _.extend(data, {
      action: '',
      name: 'file',
      data: {},
      headers: {},
      withCredentials: false,
      multiple: false,
      drag: false,
      accept: '*',
      listType: 'list',
      fileList: [],
      numMin: -Infinity,
      numMax: Infinity,
      numPerline: Infinity,
      maxSize: Config.sizeMap.GB,
      readonly: false,
      hideTip: false,
      imageWidth: Infinity,
      imageHeight: Infinity,
      imageScale: '',
      class: '',
      encType: 'multipart/form-data',
      onLoadInterceptor: data.beforeOnLoad || null,
      onErrorInterceptor: data.beforeOnError || null,
      beforeUpload: null,
      beforeRemove: null,
      autoUpload: true,
      formData: new FormData(),
    });
    this.preProcess(data);
    this.initValidation();

    this.supr(data);
  },
  onPreview(e) {
     /**
     * @event KLUpload#preview 
     * @param {object} sender 
     * @param {object} file 
     * @param {array} fileList 
     * @param {string} status 
     * @param {string} progress 
     */
    this.$emit('preview', e);
  },

  preProcess(data) {
    if (typeof data.maxSize === 'number') {
      data.maxSize += '';
    }
    if (typeof data.imageWidth === 'string') {
      data.imageWidth = parseInt(data.imageWidth);
    }
    if (typeof data.imageHeight === 'string') {
      data.imageHeight = parseInt(data.imageHeight);
    }
  },

  init(data) {
    this.initWatcher();
    this.addUploadHandler();
    this.supr(data);
  },

  initWatcher() {
    const self = this;
    this.$watch('fileList', () => {
      self.data.tip = '';
    });
  },

  addUploadHandler() {
    const self = this;
    const uploadInst = this.$refs.upload;
    const handlerList = ['success', 'progress', 'error', 'remove'];
    handlerList.forEach((handler) => {
      const handlerFnName = handler;
      uploadInst.$on(handler, self[`on${utils.camelize(handlerFnName)}`].bind(self));
    });
  },

  onSuccess(info) {
    /**
     * @event KLUpload#success 
     * @param {object} sender 
     * @param {object} file 
     * @param {array} fileList 
     * @param {string} status 
     * @param {string} progress 
     */
    this.$emit('success', info);
  },

  onProgress(info) {
    /**
     * @event KLUpload#progress 
     * @param {object} sender 
     * @param {object} file 
     * @param {array} fileList 
     * @param {string} status 
     * @param {string} progress 
     */
    this.$emit('progress', info);
  },

  onError(info) {
    /**
     * @event KLUpload#error 
     * @param {object} sender 
     * @param {object} file 
     * @param {array} fileList 
     * @param {string} status 
     * @param {string} progress 
     */
    this.$emit('error', info);
  },

  onRemove(info) {
    /**
     * @event KLUpload#remove 
     * @param {object} sender 
     * @param {object} file 
     * @param {array} fileList 
     * @param {string} status 
     * @param {string} progress 
     */
    this.$emit('remove', info);
  },

  validate() {
    const data = this.data;
    const result = { success: true, message: '' };

    function deletedFilter(file) {
      return file.flag !== Config.flagMap.DELETED;
    }

    const filteredFileList = data.fileList.filter(deletedFilter);

    if (filteredFileList.length < data.numMin) {
      result.success = false;
      result.message = this.$trans('PLEASE_UPLOAD_ATLEAST') + data.numMin + this.$trans('UNIT') + this.$trans('FILE');
      this.data.state = 'error';
    }

    data.tip = result.message;

    this.$emit('validate', {
      sender: this,
      result,
    });

    return result;
  },
})
  .component('upload-list', UploadList)
  .component('upload-card', UploadCard);

KLUpload.use(validationMixin);

module.exports = KLUpload;



