const _ = require('../../../../ui-base/_');
const ajax = require('../../../../ui-base/ajax');
const Validation = require('../../../../util/validation');
const ValidationMixin = require('../../../../util/validationMixin');

const template = require('./index.html');

/**
 * @class KLForm
 * @extend Validation
 * @param {object}      [options.data]                    = 
 * @param {string}      [options.data.service]            => source
 * @param {string}      [options.data.class]              => 
 * @param {boolean}     [options.data.inline]          => true,kl-form-iteminline-block
 * @param {string}      [options.data.sourcePath=data]    => select,json
 * @param {string/number} [options.data.labelSize]     => kl-form-itemlabelSize,kl-form-itemlabelSize
 * @param {string/number} [options.data.labelLineHeight]  => kl-form-itemlabelLineHeight,kl-form-itemlabelLineHeight
 * @param {string/number} [options.data.layout]  => : ()/vertical/inline;
 */

/**
 * @class KLFormItem
 * @extend Validation
 * @param {object}        [options.data]                    = 
 * @param {string}        [options.data.title]              => label
 * @param {string}        [options.data.titleTemplate]      => title 
 * @param {string}        [options.data.descTemplate]       => 
 * @param {number}        [options.data.cols]               => [deprecated], cols
 * @param {number}        [options.data.labelCols]          => [deprecated]title, label, labelSize
 * @param {string/number} [options.data.labelSize=200]      => title, label,px,sm, md, lg, xlg
 * @param {string}        [options.data.labelLineHeight='2.5'] => label line-height : ,sm(:1), md(:1.6), lg(:2.5)
 * @param {string}        [options.data.textAlign=none]     => label text-align none/left/right
 * @param {boolean}       [options.data.required=false]     => 
 * @param {string}        [options.data.tip]                => 
 * @param {string}        [options.data.class]              => 
 * @param {string}        [options.data.layout]          => : ()/vertical/inline;
 * @param {string}        [options.data.sourceKey]          => 
 */

const KLForm = Validation.extend({
  name: 'kl-form',
  template,
  selectors: [],
  config(data) {
    _.extend(data, {
      service: null,
      sourcePath: 'data',
      class: '',
    });

    this.supr(data);
    this.__reqSourceOnce = _.debounce(this.__reqSource.bind(this), 200, false);
  },
  init() {
    this.supr();

    this.initValidation();

    this.$watch('this.controls.length', function () {
      this.initSelectorSource();
      this.initFormItem();
    });
  },
  initFormItem() {
    const controls = this.controls;
    const {
      labelSize,
      labelLineHeight,
      layout,
    } = this.data;
    labelSize &&
      controls.forEach(($component) => {
        if (!$component.data.labelSize) {
          $component.$update('labelSize', labelSize);
        }
      });
    labelLineHeight &&
      controls.forEach(($component) => {
        if (!$component.data.labelLineHeight) {
          $component.$update('labelLineHeight', labelLineHeight);
        }
      });
    layout &&
      controls.forEach(($component) => {
        if (!$component.data.layout) {
          $component.$update('layout', layout);
        }
      });
  },
  initSelectorSource() {
    const controls = this.controls;
    this.selectors = controls.filter($formitem => !!$formitem.data.sourceKey);

    if (!this.data.service || !this.selectors.length) {
      return;
    }
    this.__reqSourceOnce();
  },
  __getSourceKeys() {
    return this.selectors.map($formitem => $formitem.data.sourceKey);
  },
  __reqSource() {
    const self = this;
    let keys = this.__getSourceKeys();

    window.NEKSelects = window.NEKSelects || {};
    keys = keys.filter(key => !window.NEKSelects[key]);

    this.selectors.forEach(($formitem) => {
      const key = $formitem.data.sourceKey;
      const source = window.NEKSelects[key] || [];
      self.__updateSource($formitem, key, source);
    });

    if (!keys.length) {
      return;
    }
    this.keys = keys;
    ajax.request({
      url: this.data.service,
      method: 'get',
      type: 'json',
      data: {
        keys: keys.join(','),
      },
      success: this.__cbReqSource.bind(this),
    });
  },
  __cbReqSource(json) {
    const self = this;
    const path = this.data.sourcePath;
    const result = (path === '' ? json : json[path]) || {};

    this.selectors.forEach(($formitem) => {
      const key = $formitem.data.sourceKey;
      const source = result[key] || [];
      self.__updateSource($formitem, key, source);
    });
    /**
       * @event KLForm#sourceCompleted kl-formsourceKey
       * @property {object} sender 
       * @property {object} result 
       */
    this.$emit('sourceCompleted', {
      sender: this,
      result: window.NEKSelects,
    });
  },
  __updateSource($formitem, key, source) {
    const $selectItem = $formitem.controls[0];

    /* :1. kl-form-item; 2. source 3. source */
    if (!$selectItem || !source.length || $selectItem.data.source.length) {
      return;
    }
    $selectItem.data.source = _.clone(source);
    window.NEKSelects[key] = _.clone(source);
    $selectItem.$update();
  },
});

/**
 * @method KLForm#validate() 
 * @return {object} conclusion 
 */
KLForm.use(ValidationMixin);

module.exports = KLForm;



