const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLCard
 * @extend Component
 * @param {object}          [options.data]                        = 
 * @param {string}          [options.data.class]                  => class
 * @param {boolean}          [options.data.isShowLine]            => title
 * @param {boolean}          [options.data.isShowBtLine]          => title
 * @param {boolean}          [options.data.isIndent]              => title
 * @param {boolean}          [options.data.bordered]              => card
 * @param {boolean}          [options.data.disHover]              => 
 */
const KLCard = Component.extend({
  name: 'kl-card',
  template,
  $tools: null,
  config() {
    _.extend(this.data, {
      title: '',
      isShowLine: true,
      isShowBtLine: true,
      isIndent: true,
    });
    this.supr();
  },
});

module.exports = KLCard;



