/**
 * ------------------------------------------------------------
 * Dropdown  
 * @author   sensen(rainforest92@126.com)
 * ------------------------------------------------------------
 */

const SourceComponent = require('../../../../ui-base/sourceComponent');
const template = require('./index.html');
const _ = require('../../../../ui-base/_');

/**
 * @class Dropdown
 * @extend SourceComponent
 * @param {object}            [options.data]                          = 
 * @param {string}            [options.data.title]                    => 
 * @param {object[]}          [options.data.source=[]]                <=> 
 * @param {string}            [options.data.source[].name]            => 
 * @param {boolean}           [options.data.source[].disabled=false]  => 
 * @param {boolean}           [options.data.source[].divider=false]   => 
 * @param {string}            [options.data.itemTemplate=null]        @=> 
 * @param {boolean}           [options.data.open=false]               <=> /
 * @param {boolean}           [options.data.disabled=false]           => 
 * @param {boolean}           [options.data.visible=true]             => 
 * @param {string}            [options.data.class]                    => class
 * @param {object}            [options.service]                       @=> 
 */
const Dropdown = SourceComponent.extend({
  name: 'dropdown',
  template,
  /**
     * @protected
     */
  config() {
    _.extend(this.data, {
      // @inherited source: [],
      itemTemplate: null,
      open: false,
    });
    this.supr();
  },
  /**
     * @method toggle(open) /
     * @public
     * @param  {boolean} open /
     * @return {void}
     */
  toggle(_open) {
    if (this.data.disabled) return;

    let open = _open;
    if (open === undefined) open = !this.data.open;
    this.data.open = open;

    // Dropdown.opens/
    const index = Dropdown.opens.indexOf(this);
    if (open && index < 0) Dropdown.opens.push(this);
    else if (!open && index >= 0) Dropdown.opens.splice(index, 1);

    /**
         * @event toggle  /
         * @property {object} sender 
         * @property {object} open /
         */
    this.$emit('toggle', {
      sender: this,
      open,
    });
  },
  /**
     * @method select(item) 
     * @public
     * @param  {object} item 
     * @return {void}
     */
  select(item) {
    if (this.data.disabled || item.disabled || item.divider) return;

    /**
         * @event select 
         * @property {object} sender 
         * @property {object} selected 
         */
    this.$emit('select', {
      sender: this,
      selected: item,
    });

    this.toggle(false);
  },
  destroy() {
    const index = Dropdown.opens.indexOf(this);
    index >= 0 && Dropdown.opens.splice(index, 1);
    this.supr();
  },
});

// dropdown
Dropdown.opens = [];

_.dom.on(document, 'click', (e) => {
  const opens = Dropdown.opens.map(d => d);
  opens.forEach((dropdown) => {
    // stopPropagationdropdowndropdown
    const element = dropdown.$refs.element;
    let element2 = e.target;
    while (element2) {
      if (element === element2) return;
      element2 = element2.parentElement;
    }
    dropdown.toggle(false);
    dropdown.$update();
  });
});

module.exports = Dropdown;



