/**
 * ------------------------------------------------------------
 * kl-select  util
 * ------------------------------------------------------------
 */

const selectUtil = {
  /**
     * keyvalue
     * @param target   
     * @param key      key
     * @param value    
     */
  clearDataToNUll(target, key, value) {
    target[key] = value || '';
  },
  /**
     * selectedkey
     * @param data          data
     * @param valueFn       selectedkey
     * @param nullValue     key
     * @param separator     key
     * @param key           key
     * @returns {string}    
     */
  getSelectedTextByKey(data, valueFn, nullValue, separator, key) {
    const selected = data.selected;
    let value = '';
    if (selected) {
      if (data.multiple) {
        value = selected.length
          ? selected.reduce((prev, next, index) => {
            let nextValue = next[key];
            if (index !== selected.length - 1) {
              nextValue += separator;
            }
            return prev + nextValue;
          }, '')
          : nullValue;
      } else {
        value = valueFn(selected);
      }
    }
    return value;
  },
};

module.exports = selectUtil;



