/**
 * @file KLMenu      
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');

/**
 * @class KLMenu
 * @extend Component
 * @param {object}        [options.data]                          = 
 * @param {string}        [options.data.class]                    => class
 * @param {boolean}       [options.data.uniqueOpened]             => 
 * @param {object}        [options.data.router]                   => , regular-statemanager
 */

/**
 * @class KLSubMenu
 * @extend Component
 * @param {object}        [options.data]                          = 
 * @param {string}        [options.data.class]                    => class
 * @param {boolean}       [options.data.defaultOpen=false]        => ,,true
 * @param {string}        [options.data.url]                   => ,url
 * @param {string}        [options.data.route]                   => spaurl
 * @param {string}        [options.data.iconClass]             => icon
 * @param {string}        [options.data.title]                    => 
 * @param {string}        [options.data.titleTemplate]            => 
 */

/**
 * @class KLMenuItem
 * @extend Component
 * @param {object}        [options.data]                          = 
 * @param {string}        [options.data.class]                    => class
 * @param {string}        [options.data.title]                    => 
 * @param {string}        [options.data.url]                      => 
 * @param {string}        [options.data.route]                    => spaurl
 * @param {boolean}       [options.data.isCurrent]                => 
 */

const KLMenu = Component.extend({
  name: 'kl-menu',
  template,
  openedMenus: [],
  currentItem: null,
  config() {
    this.defaults({
      class: '',
      uniqueOpened: true,
    });
    this.supr();
  },
  init() {
    this.$on('submenu-click', function (submenu) {
      const isOpened = this.openedMenus.indexOf(submenu) !== -1;
      if (isOpened) {
        this.closeMenu(submenu);
        this.$emit('close', submenu);
      } else {
        this.openMenu(submenu);
        this.$emit('open', submenu);
      }
    });

    /**
     * @event KLMenu#menuitem-click 
     * @property {object} menuitem menuItem
     */
    this.$on('menuitem-click', function (menuitem) {
      this.currentItem = menuitem;
      // ,, 
      this.currentSubMenu = menuitem.$outer;
    });
  },
  closeMenu(submenu) {
    this.openedMenus.splice(this.openedMenus.indexOf(submenu), 1);
  },
  openMenu(submenu) {
    const openedMenus = this.openedMenus;
    if (openedMenus.indexOf(submenu) !== -1) return;
    if (this.data.uniqueOpened) {
      this.openedMenus = [].concat(submenu);
    } else {
      this.openedMenus.push(submenu);
    }
  },
});

module.exports = KLMenu;



