const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLProgress
 * @extend Component
 * @param {object}              [options.data]                    = 
 * @param {string}              [options.data.class]              => class
 * @param {number}              [options.data.percent=36]         => 
 * @param {string}              [options.data.size=null]          => , sm/lg
 * @param {boolean}             [options.data.visible=true]       => 
 * @param {boolean}             [options.data.insideText=true]    => ,size=lg;
 * @param {string}             [options.data.color]               => 
 */
const KLProgress = Component.extend({
  name: 'kl-progress',
  template,
  config() {
    _.extend(this.data, {
      percent: 36,
      size: '',
    });
    this.supr();
  },
});

module.exports = KLProgress;



