const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLNotify
 * @extend Component
 * @param {object}      [options.data]                      = 
 * @param {string}      [options.data.position=topcenter]   => `topcenter``topleft``topright``bottomcenter``bottomleft``bottomright``static`
 * @param {number}      [options.data.duration=2000]        => `0``2`
 * @param {boolean}     [options.data.single=false]         => `true``false`
 * @param {boolean}     [options.data.visible=true]         => ,`true``false`
 * @param {string}      [options.data.class]                => `class`
 * @param {string}      [options.data.contentTemplate]      => 
 */
const KLNotify = Component.extend({
  name: 'kl-notify',
  template,
  config() {
    _.extend(this.data, {
      messages: [],
      position: 'topcenter',
      duration: 2000,
      single: false,
    });
    this.supr();
  },
  init() {
    this.supr();

    // document.body
    if (this.$root === this) this.$inject(document.body);
  },
  show(text, state, duration) {
    const self = this;
    let message = {
      text,
      state,
      duration: duration >= 0 ? +duration : +this.data.duration,
      // duration: 0,
    };
    const messages = this.data.messages;

    if (this.data.single && messages[0]) {
      message = _.extend(messages[0], message, true);
      message.counter += 1;
    } else {
      messages.unshift(message);
      message.counter = 0;
    }

    this.$update();

    if (message.duration) {
      setTimeout(() => {
        if (!message.counter) self.close(message);
        else message.counter -= 1;
      }, message.duration);
    }

    /**
       * @event KLNotify#show 
        * @property {object} sender 
        * @property {object} message 
        */
    this.$emit('show', {
      sender: this,
      message,
    });
  },
  close(message) {
    const index = this.data.messages.indexOf(message);
    if (index < 0) return;
    this.data.messages.splice(index, 1);
    this.$update();

    /**
         * @event KLNotify#close 
         * @property {object} sender 
         * @property {object} message 
         */
    this.$emit('close', {
      sender: this,
      message,
    });
  },
  closeAll() {
    this.data.messages = [];
    this.$update();
  },
});

const STATES = ['success', 'warning', 'notice', 'error'];

STATES.forEach((state) => {
  KLNotify.prototype[state] = function (text, duration) {
    this.show(text, state, duration);
  };
});

const notify = new KLNotify();
KLNotify.notify = notify;

const METHODS = [
  'show',
  'close',
  'closeAll',
  'success',
  'warning',
  'notice',
  'error',
];
KLNotify.METHODS = METHODS;


/**
 * @static
 * @param  {string} text 
 * @param  {string} state `notice``success``warning``error`
 * @param  {number} duration=2000 0notifyduration
 */
KLNotify.show = () => {};
/**
 * @static
 * @param  {object} message 
 */
KLNotify.close = () => {};
/**
 * @static
 */
KLNotify.closeAll = () => {};
/**
 * @static
 * @param  {string} text 
 * @param  {number} duration=2000 0notifyduration
 */
KLNotify.success = () => {};
/**
 * @static
 * @param  {string} text 
 * @param  {number} duration=2000 0notifyduration
 */
KLNotify.warning = () => {};
/**
 * @static
 * @param  {string} text 
 * @param  {number} duration=2000 0notifyduration
 */
KLNotify.notice = () => {};
/**
 * @static
 * @param  {string} text 
 * @param  {number} duration=2000 0notifyduration
 */
KLNotify.error = () => {};
METHODS.forEach((method) => {
  KLNotify[method] = notify[method].bind(notify);
});

module.exports = KLNotify;



