/**
 * @file KLLoading  
 * @author   sensen(rainforest92@126.com)
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');

/**
 * @class KLLoading
 * @param {object}        [options.data]                    = 
 * @param {boolean}       [options.data.static=false]       => 
 * @param {boolean}       [options.data.disabled=false]     => , showhide
 * @param {boolean}       [options.data.visible=false]      => 
 * @param {string}        [options.data.class]              => class
 * @param {string}        [options.data.el]                 => document.body
 */
const KLLoading = Component.extend({
  name: 'kl-loading',
  template,
  config() {
    _.extend(this.data, {
      static: false,
      visible: false,
    });
    this.supr();
  },
  init() {
    this.supr();

    // data.eldocument.body
    if (this.$root === this) {
      this.$inject(this.data.el || document.body);
    }
  },
  /**
     * @method KLLoading#show() 
     * @return {void}
     */
  show() {
    if (this.data.disabled) return;

    this.data.visible = true;
    this.$update();
  },
  /**
     * @method KLLoading#hide() 
     * @return {void}
     */
  hide() {
    if (this.data.disabled) return;

    this.data.visible = false;
    this.$update();
  },
});


const loading = new KLLoading();
/**
 * @param loading , NEKUI.KLLoading,
 * @static
 */
KLLoading.loading = loading;

/**
 * @static
 */
KLLoading.show = function () {
  loading.show();
};

/**
 * @static
 */
KLLoading.hide = function () {
  loading.hide();
};

module.exports = KLLoading;



