/**
 * @file Select2  
 * @author   sensen(rainforest92@126.com)
 */

import KLDrop from '../../layout/KLDrop';
import KLDropHeader from '../../layout/KLDrop/KLDropHeader';
import SourceComponent from '../../../ui-base/sourceComponent';

const template = require('./index.html');
const _ = require('../../../ui-base/_');
require('../../../util/validation');
const validationMixin = require('../../../util/validationMixin');
const Multiple = require('./plugins/multiple');
const PrivateMethod = require('./plugins/private.method');

/**
 * @class KLSelect
 * @extend Dropdown
 * @param {object}            [options.data]                          = 
 * @param {object[]}          [options.data.source=[]]                <=> 
 * @param {string}            [options.data.source[].name]            => 
 * @param {boolean}           [options.data.source[].disabled=false]  => 
 * @param {string}            [options.data.source[].tip]             => 
 * @param {string}            [options.data.source[].placement]       => 
 * @param {function}          [options.data.filter]                   =>  source  item  filter(item)  true 
 * @param {boolean}           [options.data.source[].divider=false]   => 
 * @param {object}            [options.data.selected]                 <=> 
 * @param {string/number}     [options.data.value]                    <=> 
 * @param {string}            [options.data.key=id]                   => 
 * @param {string}            [options.data.nameKey=name]             => name
 * @param {string}            [options.data.placeholder=]        => `placeholder`
 * @param {boolean}           [options.data.hideTip=false]            => 
 * @param {string}            [options.data.clearable=false]          => 
 * @param {boolean}           [options.data.required=false]           => 
 * @param {boolean}           [options.data.readonly=false]           => 
 * @param {boolean}           [options.data.disabled=false]           => 
 * @param {boolean}           [options.data.visible=true]             => 
 * @param {string}            [options.data.class]                    => class
 * @param {object}            [options.service]                       @=> 
 * @param {boolean}           [options.data.canSearch=false]          => 
 * @param {boolean}           [options.data.isCaseSensitive=false]    => 
 * @param {boolean}           [options.data.noMatchText=]      => 
 * @param {Number}            [options.data.delaySearch=300]          => 
 * @param {Number}            [options.data.maxShowCount=1000]        => 
 * @param {boolean}           [options.data.multiple=false]           => 
 * @param {boolean}           [options.data.tags=false]               => 
 * @param {string}            [options.data.separator=,]              => value
 * @param {boolean}           [options.data.selectedClose=true]       =>  
 * @param {boolean}           [options.data.canSelectAll=true]        => 
 * @param {string}            [options.data.size]                     => , sm/lgsmw/mdw/lgw
 * @param {number}            [options.data.width]                    => 
 * @param {number}            [options.data.limit]                    => limit 
 */

const KLSelect = SourceComponent.extend({
  name: 'kl-select',
  template,
  config() {
    const data = this.data;
    _.extend(data, {
      appendToBody: false,
      placement: 'bottom',
      hideTip: false,
      selected: undefined,
      key: 'id',
      nameKey: 'name',
      value: undefined,

      // 
      searchValue: '',
      canSearch: undefined,
      filter: null,
      // 
      isCaseSensitive: false,
      noMatchText: this.$trans('NO_MATCH'),
      delaySearch: 300,
      maxShowCount: 1000,

      separator: ',',
      tags: false,
      multiple: false,
      selectedClose: false,
      canSelectAll: true,
      limit: null,

      placeholder: this.$trans('PLEASE_SELECT'),
      required: false,
      clearable: false,
      // 
      key_index: -1,
    });
    if (data.multiple && !Array.isArray(data.selected)) {
      data.selected = data.selected ? [data.selected] : [];
    }
    this.supr();

    this.$watch('selected', function (newValue, oldValue) {
      // source source0source
      if (
        oldValue === undefined &&
        !(Array.isArray(data.source) && data.source.length)
      ) {
        return;
      }
      // selected[]value,
      // value
      if (oldValue === undefined && data.multiple &&
        Array.isArray(newValue) && newValue.length === 0) {
        return;
      }
      data.value = this.getValue();
      if (!newValue && data.multiple) {
        data.selected = [];
      }
      /**
       * @event KLSelect#change 
       * @property {object} sender 
       * @property {object} selected 
       * @property {string} key 
       * @property {string/number} value 
       */
      this.$emit('change', {
        sender: this,
        selected: newValue,
        key: data.key,
        value: data.value,
      });
    });

    this.$watch('value', function (newValue) {
      const source = data.source;
      const key = data.key;
      if (newValue === undefined || newValue === null) {
        data.selected = newValue;
        return;
      }

      if (source) {
        if (data.multiple) {
          const newValueArr = `${newValue}`.split(data.separator);
          data.selected = newValue
            ? source.filter(
                item => newValueArr.indexOf(`${item[key]}`) !== -1,
                this,
              )
            : [];
        } else {
          data.selected = source.find(
            item => `${item[key]}` === `${newValue}`,
            this,
          );
        }

        this.data.tip && this.validate();
      }
    });

    this.$watch('source', function (newValue) {
      if (newValue === undefined) {
        data.selected = undefined;
        return;
      }

      if (!(newValue instanceof Array)) {
        throw new TypeError('`source` is not an Array!');
      }

      const key = data.key;
      const nameKey = data.nameKey;
      const value = data.value;
      let itemHandleFn = function (_value) {
        return _value;
      };
      if (newValue.length) {
        if (
          typeof newValue[0] === 'string' ||
          typeof newValue[0] === 'number'
        ) {
          itemHandleFn = function (_value) {
            const item = {};
            item[key] = _value;
            item[nameKey] = _value;
            return item;
          };
        } else if (!newValue[0].hasOwnProperty(key)) {
          itemHandleFn = function (_value) {
            if (!_value.hasOwnProperty('divider')) {
              _value[key] = _value[nameKey];
            }
            return _value;
          };
        } else if (!newValue[0].hasOwnProperty(nameKey)) {
          itemHandleFn = function (_value) {
            _value[nameKey] = _value[key];
            return _value;
          };
        }
        data.source = newValue.map(_value => itemHandleFn(_value));
      }

      if (data.multiple) {
        if (value !== undefined && value !== null) {
          const valueArr = `${value}`.split(data.separator);
          data.selected = [].concat(
            value
              ? newValue.filter(
                  item => valueArr.indexOf(`${item[key]}`) !== -1,
                  this,
                ) || []
              : [],
          );
        } else if (data.selected) {
          data.selected = data.selected.filter(
            item => newValue.indexOf(item) !== -1,
          );
        }
      } else {
        if (value !== undefined && value !== null) {
          data.selected = newValue.find(item => `${item[key]}` === `${value}`, this);
        } else if (data.selected && newValue.indexOf(data.selected) < 0) {
          data.selected = undefined;
        }

        // placeholder
        if (!data.placeholder && !data.selected) {
          data.selected = newValue[0];
        }
      }

      /**
             * 1.isCanSearchtrue
             * 2.20 
             */
      let canSearch;
      if (this.hasOwnProperty('__canSearch')) {
        canSearch = this.__canSearch;
      } else {
        this.__canSearch = data.canSearch;
        canSearch = this.__canSearch;
      }
      data.canSearch =
        canSearch === true ||
        (Array.isArray(newValue) &&
          newValue.length > 20 &&
          canSearch !== false);
    });

    this.$watch('multiple', (newValue, oldValue) => {
      if (oldValue === undefined) {
        return;
      }
      if (newValue) {
        if (!Array.isArray(data.selected)) {
          data.selected = data.selected ? [data.selected] : [];
        }
      } else {
        data.value = '';
      }
    });
    this.$watch('limit', function (newValue, oldValue) {
      if (oldValue === undefined) {
        return;
      }
      try {
        const _newValue = Number(newValue);
        if (isNaN(_newValue)) {
          console.error(this.$trans('LIMIT_ERROR'));
        }
      } catch (e) {
        console.error(this.$trans('LIMIT_ERROR'));
      }
    });
    const $updateSource = _.throttle(
      this.$updateSource.bind(this),
      data.delaySearch,
    );
    this.$watch('searchValue', (newValue, oldValue) => {
      if (oldValue === undefined) {
        return;
      }
      data.key_index = -1;
      if (newValue) {
        /**
         * @event KLSelect#search 
         * @property {object} sender 
         * @property {string/number} value value
         */
        this.$emit('search', {
          sender: this,
          value: newValue,
        });
      }
      if (this.service && this.service.getList) {
        $updateSource();
      }
    });

    this.initValidation();
    document.addEventListener('keydown', event => this.keyup(event));
  },
  select(item) {
    const data = this.data;
    const multiple = data.multiple;
    if (
      data.readonly ||
      data.disabled ||
      (item && (item.disabled || item.divider))
    ) {
      return;
    }
    if (multiple) {
      const selected = data.selected;
      const indexOf = this.indexOf(selected, item);
      if (indexOf !== -1) {
        selected.splice(indexOf, 1);
      } else if (item) {
        selected.push(item);
      } else {
        selected.length = 0;
      }
    } else {
      data.selected = item;
    }

    /**
         * @event KLSelect#select 
         * @property {object} sender 
         * @property {object} selected 
         */
    this.$emit('select', {
      sender: this,
      selected: item,
    });
    if (multiple) {
      this.searchInputFocus();
      if (!data.selectedClose && item) {
        // 
        // data.canSearch && this.clearSearchValue();
        return;
      }
    }
    this.toggle(false);
  },
  clearContent(e) {
    e && e.stopPropagation();
    this.data.searchValue = '';
  },
  selectNone(e) {
    e && e.stopPropagation();
    this.select(undefined);
    this.data.open = false;
  },
  toggle(_open) {
    const data = this.data;
    data.canSearch && this.clearSearchValue();
    data.key_index = -1;


    let open = _open;
    if (open === undefined) open = !this.data.open;
    this.data.open = open;

    this.$emit('toggle', {
      sender: this,
      open,
    });
  },
  validate(on) {
    const data = this.data;

    // readonlydisabled, 
    if (data.readonly || data.disabled) {
      return {
        success: true,
      };
    }

    const result = { success: true, message: '' };
    let value = this.data.value;

    value = value || value === 0 ? `${value}` : '';
    if (data.required && !value.length) {
      result.success = false;
      result.message = data.message || this.$trans('PLEASE_SELECT');
      data.state = 'error';
    } else {
      result.success = true;
      result.message = '';
      data.state = '';
    }
    data.tip = result.message;

    this.$emit('validate', {
      sender: this,
      on,
      result,
    });

    return result;
  },
})
  .component('kl-drop', KLDrop)
  .component('kl-drop-header', KLDropHeader)
  .use(Multiple)
  .use(PrivateMethod)
  .use(validationMixin);

module.exports = KLSelect;



