/**
 * @file KLInput   
 * @author   sensen<rainforest92@126.com>
 * ------------------------------------------------------------
 */

const Component = require('../../../ui-base/component');
const template = require('./index.html');
const _ = require('../../../ui-base/_');
const Validation = require('../../../util/validation');
const validationMixin = require('../../../util/validationMixin');
const inputRules = require('./common/rule.js');
const inputFilters = require('./common/filter.js');

const bowser = require('bowser');

/**
 * @class KLInput
 * @extend Component
 * @param {object}          [options.data]                    = 
 * @param {string}          [options.data.value]              <=> 
 * @param {string}          [options.data.type]               => , 6int, float, charpassword email, url
 * @param {string}          [options.data.placeholder]        => 
 * @param {number}          [options.data.maxlength]          => 
 * @param {string}          [options.data.unit]               => 
 * @param {boolean}         [options.data.clearable=false]    => 
 * @param {object[]}        [options.data.rules=[]]           => 
 * @param {boolean}         [options.data.autofocus=false]    => 
 * @param {boolean}         [options.data.readonly=false]     => 
 * @param {boolean}         [options.data.disabled=false]     => 
 * @param {boolean}         [options.data.visible=true]       => 
 * @param {string}          [options.data.class]              => class
 * @param {number}          [options.data.decimalDigits]      => type=float,filter
 * @param {boolean}         [options.data.required]           => 
 * @param {boolean}         [options.data.hideTip=false]      => 
 * @param {number}          [options.data.min]                => type=int/float, type=char
 * @param {number}          [options.data.max]                => type=int/float, type=char
 * @param {string}          [options.data.message]            => 
 * @param {string}          [options.data.size]               => , sm/lgsmw/mdw/lgw
 * @param {number}          [options.data.width]              => 
 */
const KLInput = Component.extend({
  name: 'kl-input',
  template,
  config() {
    _.extend(this.data, {
      hideTip: false,
      value: '',
      type: 'char',
      placeholder: '',
      state: '',
      maxlength: undefined,
      unit: '',
      rules: [],
      autofocus: false,
      _eltIE9: bowser.msie && bowser.version <= 9,
      clearable: false,
    });
    this.rules({
      required: false,
      byteLen: this.data.type === 'char',
      isEmail: this.data.type === 'email',
      isURL: this.data.type === 'url',
      isInt: this.data.type === 'int',
      isFloat: this.data.type === 'float',
      message: '',
    });

    this.supr();

    this.initValidation();
  },
  init() {
    this.$watch('required', function (value) {
      if (value) {
        this.addRule('required');
      } else {
        this.data.rules = this.data.rules.filter(
          rule => rule.type !== 'isRequired',
        );
      }
    });
  },
  rules(ruleAttris) {
    this.supr(ruleAttris);
    const self = this;
    [
      'required',
      'isEmail',
      'isURL',
      'isFloat',
      'isInt',
      'byteLen',
    ].forEach((name) => {
      self.addRule(name);
    });
  },
  addRule(name) {
    const { min, max, message: _message, rules } = this.data;

    let message = _message;
    if (name === 'required') {
      message = message || this.$trans('PLEASE_INPUT');
    }

    if (!this.data[name]) {
      return;
    }
    const rule = inputRules[name];
    if (typeof rule === 'function') {
      rules.push(rule(min, max, message));
    } else {
      const ruleCopy = _.extend({}, rule);
      message && (ruleCopy.message = message);
      rules.push(ruleCopy);
    }
  },
  validate(on = '') {
    const { readonly, disabled } = this.data;
    // readonlydisabled, 
    if (readonly || disabled) {
      return {
        success: true,
      };
    }

    const value =
      this.data.value || this.data.value === 0 ? `${this.data.value}` : '';
    let rules = this.data.rules;

    rules = rules.filter(rule => (rule.on || '').indexOf(on) >= 0);
    if (!rules.length) {
      return { success: true };
    }
    const result = Validation.validate(value, rules);
    if (
      result.firstRule &&
      !(
        result.firstRule.silentOn === true ||
        (typeof result.firstRule.silentOn === 'string' &&
          result.firstRule.silentOn.indexOf(on) >= 0)
      )
    ) {
      this.data.tip = result.firstRule.message;
    } else this.data.tip = '';

    // @TODO
    if (!result.success) this.data.state = 'error';
    else {
      this.data.state = '';
    }

    this.$emit('validate', {
      sender: this,
      on,
      result,
    });

    return result;
  },
  clear() {
    this.$update('value', '');
  },
  /* 1. type=char,; 2. type=int/float, ; */
  __valueFilter(_value) {
    const type = this.data.type;

    let value = _value;
    if (type !== 'char') value = `${value}`.trim();
    value = (inputFilters[type] || inputFilters.default)(
      value,
      this.data.decimalDigits,
    );
    return value;
  },
  /**
    * @method KLInput#focus focus
    * @param {method}   input 
  */
  focus() {
    this.$refs.input.focus();
  },
  /**
    * @method KLInput#blur blur
    * @param {method}   input 
  */
  blur() {
    this.$refs.input.blur();
  },
  /**
    * @method KLInput#select select
    * @param {method}   input 
  */
  select() {
    this.$refs.input.select();
  },
  _onKeyUp($event) {
    this.validate('keyup');
    /**
     * @event KLInput#keyup keyup
     * @param {event} MouseEvent 
     */
    this.$emit('keyup', $event);
  },
  _onBlur($event) {
    this.validate('blur');
    /**
     * @event KLInput#blur blur
     * @param {event} MouseEvent 
     */
    this.$emit('blur', $event);
  },
  _onFocus($event) {
    /**
     * @event KLInput#focus focus
     * @param {event} MouseEvent 
     */
    this.$emit('focus', $event);
  },
  _onChange($event) {
    this.validate('change');
    /**
     * @event KLInput#change change
     * @param {event} KeyBoardEvent 
     */
    this.$emit('change', $event);
  },
  _onInput($event) {
    this.validate('input');
    /**
     * @event KLInput#input input
     * @param {event} KeyBoardEvent 
     */
    this.$emit('input', $event);
  },
  _onSearch($event) {
    /**
     * @event KLInput#search 
     * @param {event} MouseEvent 
     */
    this.$emit('search', $event);
  },
});

KLInput.filter('type', (val) => {
  if (['int', 'float'].indexOf(val) !== -1) {
    /* number, number, ++++123 */
    return 'text';
  } else if (val === 'password') {
    return 'password';
  }
  return 'text';
});

KLInput.filter({
  valueFilter: {
    get(val) {
      return this.__valueFilter(val);
    },
    set(val) {
      return this.__valueFilter(val);
    },
  },
});

KLInput.use(validationMixin);
module.exports = KLInput;



